.footer {
  background-color: black;
  height: 130px;
  color: white;
  min-width: 100%;
}
@media (max-width: 1025px) {
  .footer {
    height: 270px;
  }
}
.footer .text {
  display: flex;
  padding: 20px 0 0 0;
  margin: 0 0 -10px 0;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1025px) {
  .footer .text {
    font-size: 35px;
    margin: 10px 0 0 0;
  }
}
.footer .litle {
  font-size: 12px;
}
@media (max-width: 1025px) {
  .footer .litle {
    font-size: 35px;
  }
}
.footer .big {
  font-size: 20px;
}
@media (max-width: 1025px) {
  .footer .big {
    font-size: 50px;
    margin: 20px 0 0 0;
  }
}