.navbar {
  background-color: black;
  display: flex;
  height: 40px;
  padding: 0 0 0 120px;
}
.navbar .nav {
  margin: 0 68px;
  width: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}
.navbar .nav :last-child {
  margin: 0 0 0 -30px;
}
.navbar .nav a {
  text-decoration: none;
  padding: 5px;
  font-style: italic;
  color: white;
}
.navbar .nav .activeLink {
  color: #555;
  background-color: white;
}
.navbar .nav :active {
  color: white;
  background-color: #4682B4;
}
.navbar .nav .hr {
  background-color: white;
  width: 1px;
  height: 18px;
  margin: 0 0 0 85%;
  border-radius: 50%;
}

.navbarAdaptive {
  height: 80px;
}
.navbarAdaptive .line {
  background-color: black;
  height: 3px;
  margin: 15px;
  width: 50px;
}