.adaptiveMenu {
  background-color: #C0C0C0;
  width: 1200px;
  opacity: 0.9;
  height: 840px;
  border: 3px solid black;
}
.adaptiveMenu .close {
  width: 150px;
  float: right;
}
.adaptiveMenu .navbar .first a {
  padding: 0 0 0 150px;
}
.adaptiveMenu .navbar .nav a {
  text-decoration: none;
  font-size: 43px;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}
.adaptiveMenu .navbar .nav a :first-child {
  margin: 0 0 0 100px;
}
.adaptiveMenu .navbar .nav .hr {
  margin: 50px 0;
  background-color: black;
}