.App {
  margin: 0 auto;
  z-index: 2;
  background-color: #C0C0C0;
  display: grid;
  grid-template-areas: "h h" "n n" "c c" "f f";
  grid-template-rows: 150px 40px 1fr;
  grid-gap: 10px;
  width: 78.7%;
  max-width: 1200px;
}
@media (max-width: 1500px) {
  .App {
    width: 100%;
  }
}
@media (max-width: 1025px) {
  .App {
    grid-template-areas: "h h" "n n" "n n" "f f" "c c";
    grid-gap: 0;
  }
}
@media (max-width: 320px) {
  .App {
    width: 90%;
  }
}
.App .header {
  grid-row: h;
}
.App .navbar {
  grid-row: n;
}
@media (max-width: 1025px) {
  .App .navbar {
    display: none;
  }
}
.App .navbar2 {
  grid-row: n;
  position: absolute;
  margin: 40px 0 0 0;
  font-size: 50px;
}
@media (min-width: 1025px) {
  .App .navbar2 {
    display: none;
  }
}
.App .content {
  grid-row: c;
  background-color: white;
  min-width: 1200px;
  border: 1px solid #333;
}
@media (max-width: 1025px) {
  .App .content {
    border: none;
    margin: -40px 0 0 0;
  }
}

.global {
  background-color: #C0C0C0;
}
@media (max-width: 1025px) {
  .global {
    background-color: white;
  }
}
.global .line {
  background-color: black;
  height: 17px;
}
@media (max-width: 1200px) {
  .global .line {
    display: none;
  }
}
.global .menu {
  z-index: 3;
  position: absolute;
}

.footer {
  grid-row: f;
}
@media (max-width: 1200px) {
  .footer {
    display: none;
  }
}

.footer_adaptive {
  grid-row: f;
  margin: 50px 0 0 0;
}
@media (min-width: 1200px) {
  .footer_adaptive {
    display: none;
  }
}